<template>
  <div>
    <v-btn
      small
      rounded
      depressed
      color="btnPrimary"
      class="white--text"
      :disabled="dataFilterTwo.length === 0"
      @click="showFilterDoc = true"
    >
      <v-icon color="white" left>mdi-filter</v-icon>
      <span>{{ $t("message.64") }}</span>
    </v-btn>

    <!-- Filter by categories, for selection type fields. -->
    <v-dialog persistent v-model="showFilterDoc" max-width="500px">
      <v-card>
        <v-toolbar dense flat color="greyGrey" class="pr-1">
          <v-toolbar-title class="subtitle-1">{{
            $t("message.64")
          }}</v-toolbar-title>
          <v-spacer></v-spacer>
          <!-- Close -->
          <v-btn icon small @click="showFilterDoc = false">
            <v-icon color="error">mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-subtitle class="pt-3">{{ $t("message.38") }}</v-card-subtitle>
        <v-card-text>
          <v-row justify="center">
            <v-col
              cols="12"
              v-for="(filterTwo, idxFilter) in dataFilterTwo"
              :key="`filter-2-${idxFilter}`"
              class="mb-4"
            >
              <v-autocomplete
                dense
                :chips="filterTwo.multiple"
                :deletable-chips="filterTwo.multiple"
                :small-chips="filterTwo.multiple"
                clearable
                hide-details
                item-text="title"
                item-value="value"
                class="shrink"
                v-model="selectedDataFilterTwo[filterTwo.field]"
                :label="filterTwo.label"
                :multiple="filterTwo.multiple"
                :items="filterTwo.items"
                @input="getDocList()"
              >
                <template v-slot:prepend>
                  <v-tooltip top>
                    <template v-slot:activator="{ on }">
                      <v-btn
                        fab
                        x-small
                        depressed
                        :color="filterTwo.negation ? 'btnInfo' : 'btnRed'"
                        @click="
                          [
                            (filterTwo.negation = !filterTwo.negation),
                            getDocList(),
                          ]
                        "
                      >
                        <v-icon color="white" v-on="on">{{
                          filterTwo.negation ? "mdi-minus" : "mdi-plus"
                        }}</v-icon>
                      </v-btn>
                    </template>
                    {{ $t("message.69") }}
                  </v-tooltip>
                </template>
              </v-autocomplete>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "FilterTwo",

  props: {
    getDocList: {
      type: Function,
      required: true,
    },
  },

  data: () => ({
    showFilterDoc: false,
  }),

  computed: {
    ...mapState("docList", ["dataFilterTwo", "selectedDataFilterTwo"]),
  },
};
</script>
